
import { Navigate } from 'react-router-dom';
import DashboardLayout from 'src/components/admin/layout/DashboardLayout';
import Login from 'src/pages/admin/Login';
import NotFound from 'src/pages/NotFound';
import StoryList from 'src/pages/admin/StoryList';;
import AddEditStory from 'src/pages/admin/AddEditStory';
import StoryPage from 'src/pages/publicFacing/StoryPage';
import PublicFacingStoryList from 'src/pages/publicFacing/PublicFacingStoryList';
import AnimationList from 'src/pages/publicFacing/AnimationList';
import WebAnimation from 'src/pages/admin/WebAnimation';
import PublicFacingAboutUs from "src/pages/publicFacing/PublicFacingAboutUs";
import AboutUs from 'src/pages/admin/AboutUs';
import EditAboutUs from 'src/pages/admin/EditAboutUs';
import AnimationGrid from 'src/pages/publicFacing/AnimationGrid';
import ContactPage from './pages/publicFacing/ContactPage';
import PublicLayout from './components/publicFacing/layout/PublicLayout';

const routes = [
  {
    path: "admin",
    element: <DashboardLayout />,
    children: [
      { path: "/", element: <Navigate to="/admin/flowers" /> },
      { path: "/manageFlowers", element: <Navigate to="/admin/flowers" /> },
      { path: "/manageMezzuzot", element: <Navigate to="/admin/mezzuzot" /> },
      { path: "login", element: <Login /> },
      { path: 'flowers', element: <StoryList resourceType={'Flowers'} resourceTypeSingular={"Flower"}/> },
      { path: 'flowers/new', element: <AddEditStory resourceType={'Flowers'} /> },
      { path: 'flowers/:id', element: <AddEditStory resourceType={'Flowers'} /> },
      { path: 'manageAboutUs', element: <AboutUs /> },
      { path: 'manageAboutUs/:id', element: <EditAboutUs /> },
      { path: "webAnimation", element: <WebAnimation /> },
      { path: "mezzuzot", element: <StoryList resourceType={'Mezzuzot'} resourceTypeSingular={"Mezzuzah"}/> },
      { path: "404", element: <NotFound /> },
      { path: "mezzuzot/new", element: <AddEditStory resourceType={'Mezzuzot'} /> },
      { path: "mezzuzot/:id", element: <AddEditStory resourceType={'Mezzuzot'} /> },
      { path: '*', element: <Navigate to="/admin/404" /> }
    ]
  },
  { path: "/", element: <Navigate to="/flowerStory/list" /> },
  { path: "*", element: <Navigate to="/404" /> },
  { path: "login", element: <Login /> },
  {
    path: "/",
    element: <PublicLayout />,
    children: [
      { path: "flowerStory/list", element: <PublicFacingStoryList resourceType={'Flowers'} /> },
      { path: "mezzuzahStory/list", element: <PublicFacingStoryList resourceType={'Mezzuzot'} /> },
      { path: 'animations/:id', element: <AnimationList /> },
      { path: 'animations', element: <AnimationGrid /> },
      { path: "aboutUs", element: <PublicFacingAboutUs resourceType={'Us'}/> },
      { path: "aboutFlowers", element: <PublicFacingAboutUs resourceType={'Glass Flowers Project'}/> },
      { path: "aboutMezzuzot", element: <PublicFacingAboutUs resourceType={'Mezzuzot Project'}/> },
      { path: "contact", element: <ContactPage /> },
      { path: "404", element: <NotFound /> },
      {
        path: "flowerStory/:id",
        element: <StoryPage resourceType={'Flowers'} resourceTypeSingular={'Flower'} />,
      },
      {
        path: "mezzuzahStory/:id",
        element: <StoryPage resourceType={'Mezzuzot'} resourceTypeSingular={'Mezzuzah'} />,
      },
    ],
  },

];

export default routes;
